import { configure, use, run } from 'platojs/system'

import logger from 'modules/logger'
import persist from 'modules/persist'
import i18n from 'modules/i18n'
import validator from 'modules/validator'
import core from 'modules/core'

import Root from './views/root'

import news from 'modules/news'
/**
 * 
 */
configure({
  // 
  name: 'coding',
  // 
  version: '1.0.0',
  //  component  element
  element: '#app',
  component: Root
})

/**
 * Use Modules
 */

/**
 * 
 */
__DEV__ && use(logger)

/**
 * 
 */
use(i18n)
use(validator)

/**
 * 
 */
// use(xxx, { prefix: 'xxx' })
// use(yyy, { prefix: 'yyy' })
// use(zzz, { prefix: 'zzz' })
use(news)
/**
 * 
 */
use(core)
use(persist)

/**
 * Run Modules
 */

run(({ router }) => {
  __PROD__ || console.log('%c[PLATO] %cLet\'s go!',
    'font-weight: bold', 'color: green; font-weight: bold')

  /**
   * Let's go!
   */
  router.afterEach(() => {
    //  iOS  BUG
    const activeElement = document.activeElement
    if (activeElement && activeElement.nodeName !== 'BODY') {
      activeElement.blur()
    }
  })
})



